.Logo {
  /* FLEXBOX */
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  margin-left: 15vw;
  margin-right: 7vw;
  min-width: 185.53px;
  font-family: "Newsreader", serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
  font-size: 28px;
  text-align: center;
  /* STYLE */
  user-select: none;
  color: #f1f1f1;
  text-underline-offset: 5px;
}

.Logo-text {
  display: flex;
  align-items: center;
  margin: 0px;
  position: relative;
  padding-top: 2px;
}

.fa-cat {
  padding: 5px;
  transform: translateY(-3px);
  font-size: 20px;
  display: none;
}

.Logo-text:hover .fa-cat {
  display: block;
}

.fa-cat:hover {
  user-select: none;
  color: #f1f1f1;
}

@media (max-width: 1000px) {
  .Logo-text:hover .fa-cat {
    display: none;
  }
  .fa-cat:hover {
    user-select: none;
    color: #f1f1f1;
  }
}
@media (max-width: 750px) {
  .Logo {
    margin-left: 28px;
    font-size: 25px;
    justify-content: flex-start;
  }
  .Logo-text {
    padding-top: 4px;
  }
}

