.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #0f0f0f;
}

.Article-ref {
  width: 100%;
  z-index: 1;
}

@media (max-width: 750px) {
  .App {
    background-color: #242424;
  }
  .break {
    content: "";
    margin: 2em;
    display: block;
    font-size: 15%;
  }
}

